  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;

  background-color: beige;
  color: #333;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: 0% 18%;
  background-size: cover;
}

.weather-icon img {
  width: 280px;
  padding: 0;
}

/* HEADER  */
h1 {
  text-align: center;
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 20px;
}

/* SEARCH BAR  */
.search-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.search-field {
  flex: 1;
  width: 75%;
}

input {
  width: 100%;
  height: 2.5em;
  padding-left: 12px;
  padding-right: 12px;
  margin-top: 7px;
  appearance: none;
  border: none;
  border-radius: 10px;
}

input:focus {
  outline: 3px solid green;
}

/* ERROR  */
.error {
  opacity: 0;
}

.active {
  opacity: 1;
  height: 2em;
  text-align: center;
  display: block;
  background-color: coral;
  margin: 0;
  width: 100%;
  border: 1px solid white;
  border-radius: 8px;
  color: whitesmoke;
}

/* SEARCH BUTTON  */
.search-button {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 50px;
}

.search-button img {
  width: 70px;
}

.search-button img.inactive {
    opacity: 0;
}

.search-button button {
  appearance: none;
  color: #eee;
  font-weight: bold;
  background-color: green;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
}

/* SECTION  */
section {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

/* INFOS  */
.weather-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 1.2em;
  margin-left: 20px;
  margin-top: 20px;
}
